.Contactme{
    background-color: #cccccc;
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.ContactOptions{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
}

.ContactOption{
    width: 120px;
    height: 35px;
    background-color: #333333;
    color: floralwhite;
    border: 1px solid #ccc;
    border-radius: 10px;
    margin: 10px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 2px 3px #555555;
}

.ContactOption:hover{
    background-color: #666666;
    transform: translateY(4px);
}

.ContactOption:active{
    transform: translateY(6px);
}

.Or{
    margin: 10px;
    font-size: 1.5em;
}

.ContactForm{
    width: 100%;
    max-width: 350px;
}

.ContactFormSubmit{
    width: 120px;
    height: 35px;
    background-color: #333333;
    color: floralwhite;
    border: 1px solid #ccc;
    border-radius: 10px;
    margin: 0px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 2px 3px #555555;
    margin-top: 10px;
}

.ContactFormSubmit:hover{
    background-color: #666666;
    transform: translateY(4px);
}

.ContactFormSubmit:active{
    transform: translateY(6px);
}

.FormLabel{
    width: 100%;
}

.FormInput{
    width: 100%;
    border: 0px;
    outline: none;
    height: 30px;
    font-size: 1.25em;
    margin-bottom: 10px;
    border-radius: 5px;
}

.FormTextArea{
    width: 100%;
    height: 150px;
    border: 0px;
    outline: none;
    border-radius: 5px;
    font-size: 1.5em;
}

.FormButtons{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.SendOtherMsg{
    width: 200px;
    height: 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: #333333;
    color: floralwhite;
    border: 1px solid #ccc;
    border-radius: 10px;
    margin: 0px 10px;
    box-shadow: 2px 3px #555555;
}

.SendOtherMsg:hover{
    background-color: #666666;
    transform: translateY(4px);
}

.SendOtherMsg:active{
    transform: translateY(8px);
}