.Card{
    border: 2px solid #666666;
    max-width: 200px;
    width: 90%;
    margin: 10px 30px;
    background-image: linear-gradient(to bottom, transparent, #999999);
    height: 200px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    transition:max-width 0.3s ease-out, height 0.3s ease-out;
}

.Card:hover{
    width: 95%;
    max-width: 250px;
    height: 250px;
}

.Card:hover .CardButton{
    display: flex;
}

.CardButton{
    width: 100px;
    height: 40px;
    background-color: #333333;
    color: floralwhite;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #666666;
    box-shadow: 2px 3px #666666;
    border-radius: 10px;
    display: none;
}

.CardButton:hover{
    background-color: #555555;
    transform: translateY(4px);
}

.CardButton:active{
    transform: translateY(8px);
}

.Img{
    width: 70px;
    height: 50px;
}

.Technologies{
    margin:20px;
}