.Home{
    background-image:linear-gradient(to bottom,transparent, #333333), url('../../assets/images/bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.HomeDetails{
    width: 50%;
    color: floralwhite;
}

.Name{
    font-size: 4em;
}

.Designation{
    font-size: 2.5em;
}

.Education{
    font-size: 1.5em;
}

.Button{
    background-color: #333333;
    outline: none;
    width: 170px;
    height: 40px;
    margin: 30px;
    border: 1px solid #ccc;
    border-radius: 20px;
    box-shadow: 2px 3px #ccc;
}

.Button:hover {
    background-color: #666666;
    transform: translateY(4px);
}

.Button:active {
  background-color: #666666;
  box-shadow: 0 5px #ccc;
  transform: translateY(4px);
}

.AButton{
    text-decoration: none;
    color: floralwhite;
}

.AButton:hover{
    color: floralwhite;
}

.SocialIcons{
    text-decoration: none;
    color: floralwhite;
    font-size: 3em;
    margin: 15px;
}

.SocialIcons:hover{
    color: grey;
}

.SocialIcons:active{
    color: floralwhite;
}

@media (max-width: 650px) {
	.Home{
		background-image:linear-gradient(to bottom,transparent, #333333), url('../../assets/images/MyImage.jpeg');
    }

    .Social{
        text-align: center;
    }

    .HomeDetails{
        width: 80%;
        color: floralwhite;
    }

    .Name{
        text-align: center;
        font-size: 2.5em;
    }

    .Details{
        text-align: center;
    }
    
    .Designation{
        font-size: 1.5em;
    }
    
    .Education{
        font-size: 1.5em;
    }

    .Buttons{
        text-align: center;
    }

    .Button{
        margin: 10px;
    }
}